<template>
  <div class="table1">
    <div class="add cb">
      <div class="add-left fl">人员信息</div>
      <div class="add-right fr">
        <button @click="dialogFormVisible = true">新增信息</button>   
        <button @click="add()" :plain="true">新增评价</button>
      </div>
    </div>
    <el-table ref="multipleTable" :data="personList" tooltip-effect="dark" type="index" style="width: 100%"  @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="70">
      </el-table-column>
       <el-table-column prop="staff" label="姓名" show-overflow-tooltip>
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row|showStaffName}}</div>
        </template>
      </el-table-column>
      
      <el-table-column prop="baseAddress" label="籍贯" width="110">
        <template scope="scope" >
          <div @click="examineOper(scope.row)">{{scope.row.baseAddress}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="school" label="毕业学校" width="130">
        <template scope="scope">
          <div @click="examineOper(scope.row)">{{scope.row.school}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="major" label="专业"  width="110">
        <template scope="scope">
          <div @click="examineOper(scope.row)">{{scope.row.major}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="groupTypevalue" label="归属小组"  width="110">
        <template scope="scope">
          <div @click="examineOper(scope.row)">{{scope.row.groupTypevalue}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="staffTeacherName" label="导师名称"  width="120">
        <template scope="scope">
          <div @click="examineOper(scope.row)">{{scope.row.staffTeacherName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="beginDate" label="入司时间" width="150">
        <template scope="scope">
          <div @click="examineOper(scope.row)">{{scope.row.beginDate}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="140">
        <template scope="scope">
            <el-button type="text" @click="deletelist(personList.staffId,scope.$index)"><i class="el-icon-delete delete"></i></el-button>
            <el-button type="text" @click="edit(scope.row)" ><i class="el-icon-edit"></i></el-button>
        </template>
			</el-table-column>
		</el-table>
    <el-dialog title="编辑" :visible.sync="dialogVisible" width="60%" size="mini" class="main-content">
      <el-form :label-position="labelPosition" :inline="true" label-width="130px" :rules="rules" :model="editDetail" size="mini" ref="formName">
         <el-form-item label="人员" prop="staffId">
          <template>
            <el-select v-model="editDetail.staffId" placeholder="请选择人员"  >
              <el-option v-for="item in staffId" :key="item.staffId" :label="item.staffname"  :value="item.staffId"></el-option>
            </el-select>
          </template>
        </el-form-item>
        <el-form-item label="学校" prop="school" >
          <el-input v-model="editDetail.school" placeholder="学校" name="school"></el-input>
        </el-form-item>
        <el-form-item label="籍贯" prop="baseAddress">
          <el-input v-model="editDetail.baseAddress" placeholder="籍贯" name="baseAddress"></el-input>
        </el-form-item>
        <el-form-item label="导师名称" prop="staffTeacherName">
          <el-input v-model="editDetail.staffTeacherName" placeholder="导师名称" name="staffTeacherName"></el-input>
        </el-form-item>
        <el-form-item label="专业" prop="major">
          <el-input v-model="editDetail.major" placeholder="专业" name="major"></el-input>
        </el-form-item>
        <el-form-item label="归属组" prop="staffGroupType">
          <template>
            <el-select v-model="editDetail.staffGroupType" placeholder="请选择归属小组"  >
              <el-option v-for="item in staffGroupType" :key="item.typeId" :label="item.typeValue"  :value="item.typeId"></el-option>
            </el-select>
          </template>
        </el-form-item>
        <el-form-item label="入司日期" prop="beginDate" >
          <template>
           <el-date-picker  v-model="editDetail.beginDate" type="date"  placeholder="选择入司日期" value-format="yyyy-MM-dd">
           </el-date-picker>
          </template>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="update(editDetail,'formName')" size="small">更新</el-button>
        <el-button type="primary" @click="dialogVisible = false" size="small">取消</el-button>
      </span>
    </el-dialog>
    <el-dialog title="新增" :visible.sync="dialogFormVisible" width="60%" size="mini" class="main-content">
      <el-form :label-position="labelPosition" :inline="true" label-width="130px" :rules="rules" :model="addDetail" size="mini" ref="formName">
       <el-form-item label="人员" prop="staffId">
          <template>
            <el-select v-model="addDetail.staffId" placeholder="请选择人员"  >
              <el-option v-for="item in staffId" :key="item.staffId" :label="item.staffname"  :value="item.staffId"></el-option>
            </el-select>
          </template>
        </el-form-item>
        <el-form-item label="学校" prop="school" >
          <el-input v-model="addDetail.school" placeholder="学校" name="school"></el-input>
        </el-form-item>
        <el-form-item label="籍贯" prop="baseAddress">
          <el-input v-model="addDetail.baseAddress" placeholder="籍贯" name="baseAddress"></el-input>
        </el-form-item>
        <el-form-item label="导师名称" prop="staffTeacherName">
          <el-input v-model="addDetail.staffTeacherName" placeholder="导师名称" name="staffTeacherName"></el-input>
        </el-form-item>
        <el-form-item label="专业" prop="major">
          <el-input v-model="addDetail.major" placeholder="专业" name="major"></el-input>
        </el-form-item>
        <el-form-item label="归属组" prop="staffGroupType">
          <template>
            <el-select v-model="addDetail.staffGroupType" placeholder="请选择归属小组"  >
              <el-option v-for="item in staffGroupType" :key="item.typeId" :label="item.typeValue"  :value="item.typeId"></el-option>
            </el-select>
          </template>
        </el-form-item>
        <el-form-item label="入司日期" prop="beginDate" >
          <template>
           <el-date-picker  v-model="addDetail.beginDate" type="date"  placeholder="选择入司日期" value-format="yyyy-MM-dd">
           </el-date-picker>
          </template>
        </el-form-item>
          
      </el-form>
      <span slot="footer" class="dialog-footer">
            <el-button @click="adddetail(addDetail,'formName')" size="small">新增</el-button>
            <el-button type="primary" @click="dialogFormVisible= false" size="small">取消</el-button>
          </span>
    </el-dialog>
    <div class="hello">      
      <el-pagination  @size-change="pageSizeChange" @current-change="currentPageChange" :current-page="currentPage" :page-sizes="[ 5,10 ]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination> 
    </div>
  </div>
</template>

<script>
import service from "../scripts/service.js";
export default {
  name: "table1",
  data() {
    return {
      rules: {
        staffId: [
          {
            required: true,
            message: "请选择人员",
            trigger: "blur"
          }
        ],
        baseAddress: [
          {
            required: true,
            message: "请输入籍贯",
            trigger: "blur"
          }
        ],
        staffTeacherName: [
          {
            required: true,
            message: "请输入导师名称",
            trigger: "blur"
          }
        ],
        major: [
          {
            required: true,
            message: "请输入专业",
            trigger: "blur"
          }
        ],
        staffGroupType: [
          {
            required: true,
            message: "请选择归属小组",
            trigger: "blur"
          }
        ],
        school: [
          {
            required: true,
            message: "请输入学校名称",
            trigger: "blur"
          }
        ],
        beginDate: [
          {
            required: true,
            message: "请选择入司日期",
            trigger: "blur"
          }
        ]
      },
      dialogVisible: false,
      dialogFormVisible: false,
      addDetail: {
        staffId: "",
        staffGroupType: "",
        beginDate: ""
      },
      staffId: "",
      staffGroupType: "",
      beginDate: "",
      editDetail: {},
      index: 0,
      personList: [],
      subjectName: "",
      staffname: "",
      staffID: "",
      editid: "",
      checkStaffId: "",
      //多选值
      multipleSelection: [],
      //当前页
      currentPage: 1,
      //分页大小
      pageSize: 10,
      //总记录数
      total: ""
    };
  },
  mounted: function() {
    this.getselect();
    this.getAauthor();
    this.getList();
  },
  filters: {
    showStaffName(row) {
      if (row.staff) {
        return row.staff.staffname;
      } else {
        return "";
      }
    }
  },
  methods: {
    getselect() {
      service
        .getListGroupType()
        .then(res => {
          this.staffGroupType = res.data;
        })
        .catch(err => {
          console.log(err);
        });
      service
        .getListSxByGType()
        .then(res => {
          this.staffId = res.data;
          console.log(this.staffId);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getAauthor() {
      service
        .getListSxByGType()
        .then(res => {
          console.log(res);
          this.Aauthor = res.data[4];
          console.log(this.Aauthor);
          this.staffname = this.Aauthor.staffname;
          this.staffID = this.Aauthor.staffId;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getList() {
      let params = {
        pages: this.currentPage,
        pageSize: this.pageSize
      };
      console.log(params);
      service
        .selectListAll(params)
        .then(res => {
          console.log(res);
          // 将获取回来的数据赋值给list
          this.personList = res.data.list;
          this.total = res.data.total;
          console.log(this.personList);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 删除
    deletelist(staffId, index) {
      this.$confirm("确认删除？")
        .then(_ => {
          let params = {
            staffId: this.personList[index].staffId
          };
          console.log(params);
          service
            .delOneStaffInfo(params)
            .then(response => {
              console.log(response);
              // 加载列表
              this.getList();
            })
            .catch(error => {
              console.log(error);
              alert("删除失败！");
            });
        })
        .catch(_ => {});
    },
    //新增人员基本信息
    adddetail(formName) {
      this.$refs["formName"].validate(valid => {
        if (valid) {
          let params = {
            csi: this.addDetail
          };
          console.log(params);
          service
            .addOneStaffInfo(params)
            .then(response => {
              alert("submit!");
              this.getList();
            })
            .catch(error => {
              console.log(error);
            });
          this.dialogFormVisible = false;
        } else {
          return false;
        }
      });
    },
    //编辑
    edit(item) {
      this.editDetail = {
        major: item.major,
        staffTeacherName: item.staffTeacherName,
        staffGroupType: item.staffGroupType,
        baseAddress: item.baseAddress,
        school: item.school,
        beginDate: item.beginDate,
        staffId: item.staffId
      };
      this.dialogVisible = true;
      this.editid = item.staffId;
    },
    //确认更新
    update(knowId, formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = {
            csi: this.editDetail
          };
          console.log(params);
          service
            .updateOneStaffInfo(params)
            .then(response => {
              this.getList();
            })
            .catch(error => {
              console.log(error);
            });
          this.dialogVisible = false;
          alert("submit!");
        } else {
          return false;
        }
      });
    },
    // 新增考核评价
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    add(rows) {
      console.log(this.multipleSelection);
      console.log(this.multipleSelection.length);
      if (this.multipleSelection.length == 1) {
        // console.log(this.multipleSelection);
        // console.log(this.multipleSelection[0].staff.staffId);
        this.checkStaffId = this.multipleSelection[0].staff.staffId;
        let routeData = this.$router.resolve({
          path: "/examineCreat",
          query: {
            checkStaffId: this.checkStaffId,
            staffId: this.staffID
          }
        });
        window.open(routeData.href, "_blank");
      } else if (this.multipleSelection.length == 0) {
        this.$message("请选择用户");
        console.log("请选择用户");
      } else {
        console.log("只能选择一个用户");
        this.$message("只能选择一个用户");
      }
    },
    //查看考核详情
    examineOper(row) {
      console.log(row.staffId);
      let routeData = this.$router.resolve({
        path: "/examineDetail",
        query: {
          checkStaffId: row.staffId
        }
      });
      window.open(routeData.href, "_blank");
    },
    //分页大小修改事件
    pageSizeChange: function(val) {
      console.log("每页 " + val + " 条");
      this.pageSize = val;
      this.getList();
    },
    //当前页修改事件
    currentPageChange: function(val) {
      this.currentPage = val;
      console.log("当前页: " + val);
      this.getList();
    }
  }
};
</script>

<style scoped>
table {
  width: 100%;
  font-size: 14px;
  border: 1px solid #eee;
}
table thead th {
  background: #e5eff8;
  padding: 22px 18px 22px 18px;
  text-align: center;
}
table tbody td {
  padding: 10px 0px 10px 18px;
  text-align: left;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
}
table span {
  margin: 0 10px;
  cursor: pointer;
}
.delete {
  color: red;
}
.edit {
  color: #008cd5;
}
.add {
  border: 1px solid #eee;
  margin-bottom: 10px;
  height: 65px;
  padding: 15px 0px 15px 15px;
}
tbody tr:hover {
  background-color: #e5f0f86c;
}
.add button {
  background: #008cd5;
  border: 0;
  cursor: pointer;
  padding: 6px 15px;
  border-radius: 3px;
  color: #fff;
  margin-left: 10px;
}
.hello {
  margin-top: -10px;
  margin-bottom: 10px;
}
.header-search-input {
  display: inline-block;
  right: 1%;
  margin-left: 12px;
  width: 300px;
}
.header-search-input input {
  position: relative;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #d8dce5;
  display: inline-block;
  height: 35px;
  line-height: 1;
  outline: 0;
  padding: 0 25px 0 15px;
  width: 280px;
  margin-right: -4px;
}
.query-search-btn {
  color: #c2d6e7;
  position: relative;
  display: inline-block;
  margin-left: -30px;
  cursor: pointer;
}
.add-left {
  font-size: 16px;
}
.hello {
  position: fixed;
  bottom: 0px;
  right: 20px;
}
.add-right {
  margin-right: 80px;
}
</style>



